// petService 單元測試 - 簡化版本，不依賴數據庫
import { describe, it, expect, jest, beforeEach, afterEach } from '@jest/globals';

// 設置測試環境變數
process.env.NODE_ENV = 'test';
process.env.JWT_SECRET = 'test-jwt-secret-key-with-minimum-32-characters-length';

// 模擬外部依賴
jest.mock('../../src/models/Pet');
jest.mock('../../src/models/User');
jest.mock('../../src/services/cloudinaryService');
jest.mock('../../src/services/aiService');
jest.mock('../../src/utils/logger');

// 導入要測試的服務
import { PetService, CreatePetData, UpdatePetData } from '../../src/services/petService';

// 模擬的 Pet 模型
const mockPet = {
  _id: '507f1f77bcf86cd799439011',
  name: '小白',
  type: 'dog',
  breed: '拉布拉多',
  status: 'lost',
  owner: '507f1f77bcf86cd799439012',
  save: jest.fn(),
  populate: jest.fn().mockReturnThis(),
  createdAt: new Date(),
  updatedAt: new Date()
};

// 模擬的 User 模型
const mockUser = {
  _id: '507f1f77bcf86cd799439012',
  username: 'testuser',
  email: 'test@example.com'
};

// 創建模擬構造函數
const MockPet = jest.fn().mockImplementation((data) => ({
  ...mockPet,
  ...data,
  save: jest.fn().mockResolvedValue({ ...mockPet, ...data }),
  populate: jest.fn().mockResolvedValue({ ...mockPet, ...data })
}));

// 添加靜態方法到模擬構造函數
MockPet.findById = jest.fn().mockReturnValue({
  populate: jest.fn().mockReturnValue({
    exec: jest.fn().mockResolvedValue(mockPet)
  })
});
MockPet.findOne = jest.fn();
MockPet.findByIdAndUpdate = jest.fn();
MockPet.findByIdAndDelete = jest.fn();
MockPet.find = jest.fn();
MockPet.countDocuments = jest.fn();
MockPet.deleteMany = jest.fn();

// 模擬 User 模型的靜態方法
const MockUser = {
  findById: jest.fn(),
  deleteMany: jest.fn()
};

// 模擬 CloudinaryService
const MockCloudinaryService = {
  uploadImage: jest.fn(),
  deleteImage: jest.fn()
};

// 模擬 aiService
const MockAiService = {
  analyzeImage: jest.fn()
};

// 設置模擬
jest.doMock('../../src/models/Pet', () => ({ Pet: MockPet }));
jest.doMock('../../src/models/User', () => ({ User: MockUser }));
jest.doMock('../../src/services/cloudinaryService', () => ({ CloudinaryService: MockCloudinaryService }));
jest.doMock('../../src/services/aiService', () => ({ aiService: MockAiService }));

describe('PetService 測試', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    
    // 設置預設的模擬回應
    MockUser.findById.mockResolvedValue(mockUser);
    MockPet.findById.mockReturnValue({
      populate: jest.fn().mockReturnValue({
        exec: jest.fn().mockResolvedValue(mockPet)
      })
    });
    mockPet.save.mockResolvedValue(mockPet);
    mockPet.populate.mockResolvedValue(mockPet);
  });
  
  afterEach(() => {
    jest.restoreAllMocks();
  });

  describe('createPet', () => {
    const petData: CreatePetData = {
      name: '小花',
      type: 'cat',
      breed: '波斯貓',
      status: 'lost',
      description: '走失的小貓',
      location: {
        address: '台北市信義區',
        city: '台北市',
        coordinates: [121.5654, 25.0330]
      },
      contact: {
        phone: '0912345678',
        email: 'owner@example.com'
      }
    };

    it('應該成功創建寵物', async () => {
       const result = await PetService.createPet('507f1f77bcf86cd799439012', petData);
       
       expect(MockUser.findById).toHaveBeenCalledWith('507f1f77bcf86cd799439012');
       expect(MockPet).toHaveBeenCalled();
       expect(result).toBeDefined();
       expect(result.name).toBe(petData.name);
     });

    it('應該在擁有者不存在時拋出錯誤', async () => {
       MockUser.findById.mockResolvedValue(null);
       
       await expect(PetService.createPet('507f1f77bcf86cd799439999', petData))
          .rejects.toThrow('擁有者不存在');
     });

    it('應該處理圖片上傳', async () => {
      const petDataWithImages = {
        ...petData,
        images: ['image1.jpg', 'image2.jpg']
      };
      
      MockCloudinaryService.uploadImage.mockResolvedValue({
         secure_url: 'https://cloudinary.com/test.jpg',
         public_id: 'test_id',
         width: 800,
         height: 600
       });
       
       await PetService.createPet('507f1f77bcf86cd799439012', petDataWithImages);
       
       expect(MockCloudinaryService.uploadImage).toHaveBeenCalledTimes(2);
    });
  });

  describe('getPetById', () => {
    it('應該成功根據ID獲取寵物', async () => {
      const result = await PetService.getPetById('507f1f77bcf86cd799439011');
        
        expect(MockPet.findById).toHaveBeenCalledWith('507f1f77bcf86cd799439011');
        expect(result).toBeDefined();
        expect(result?._id).toBe('507f1f77bcf86cd799439011');
    });

    it('應該在ID無效時拋出錯誤', async () => {
      await expect(PetService.getPetById('invalid-id'))
        .rejects.toThrow('無效的寵物 ID');
    });

    it('應該返回null當寵物不存在', async () => {
       MockPet.findById.mockReturnValue({
         populate: jest.fn().mockReturnValue({
           exec: jest.fn().mockResolvedValue(null)
         })
       });
       
       const result = await PetService.getPetById('507f1f77bcf86cd799439011');
       
       expect(result).toBeNull();
     });
  });

  describe('updatePet', () => {
    const updateData: UpdatePetData = {
      name: '更新的小白',
      description: '更新的描述'
    };

    it('應該成功更新寵物', async () => {
       MockPet.findOne.mockResolvedValue(mockPet);
       MockPet.findByIdAndUpdate.mockReturnValue({
         populate: jest.fn().mockResolvedValue({ ...mockPet, ...updateData })
       });
       
       const result = await PetService.updatePet('507f1f77bcf86cd799439011', '507f1f77bcf86cd799439012', updateData);
        
        expect(MockPet.findOne).toHaveBeenCalledWith({ _id: '507f1f77bcf86cd799439011', owner: '507f1f77bcf86cd799439012' });
       expect(MockPet.findByIdAndUpdate).toHaveBeenCalled();
       expect(result?.name).toBe(updateData.name);
     });

    it('應該在寵物不存在時拋出錯誤', async () => {
       MockPet.findOne.mockResolvedValue(null);
       
       await expect(PetService.updatePet('507f1f77bcf86cd799439011', '507f1f77bcf86cd799439012', updateData))
          .rejects.toThrow('寵物不存在或您沒有權限修改');
     });
  });

  describe('deletePet', () => {
    it('應該成功刪除寵物', async () => {
       MockPet.findOne.mockResolvedValue({
         ...mockPet,
         images: [{ publicId: 'test_id', url: 'test.jpg' }]
       });
       MockPet.findByIdAndDelete.mockResolvedValue(mockPet);
       MockCloudinaryService.deleteImage.mockResolvedValue(true);
       
       const result = await PetService.deletePet('507f1f77bcf86cd799439011', '507f1f77bcf86cd799439012');
        
        expect(MockPet.findOne).toHaveBeenCalledWith({ _id: '507f1f77bcf86cd799439011', owner: '507f1f77bcf86cd799439012' });
        expect(MockPet.findByIdAndDelete).toHaveBeenCalledWith('507f1f77bcf86cd799439011');
       expect(MockCloudinaryService.deleteImage).toHaveBeenCalledWith('test_id');
       expect(result).toBe(true);
     });

    it('應該在寵物不存在時拋出錯誤', async () => {
       MockPet.findOne.mockResolvedValue(null);
       
       await expect(PetService.deletePet('507f1f77bcf86cd799439011', '507f1f77bcf86cd799439012'))
          .rejects.toThrow('寵物不存在或您沒有權限刪除');
     });
  });

  describe('getAllPets', () => {
    it('應該成功獲取所有寵物', async () => {
      const mockPets = [mockPet, { ...mockPet, _id: '507f1f77bcf86cd799439013', name: '小黑' }];
      
      MockPet.find.mockReturnValue({
         populate: jest.fn().mockReturnValue({
           sort: jest.fn().mockReturnValue({
             skip: jest.fn().mockReturnValue({
               limit: jest.fn().mockReturnValue({
                 exec: jest.fn().mockResolvedValue(mockPets)
               })
             })
           })
         })
       });
       
       MockPet.countDocuments.mockResolvedValue(2);
      
      const result = await PetService.getAllPets(1, 10);
      
      expect(result.pets).toHaveLength(2);
      expect(result.total).toBe(2);
      expect(result.page).toBe(1);
      expect(result.totalPages).toBe(1);
    });
  });

  describe('incrementViewCount', () => {
    it('應該成功增加瀏覽次數', async () => {
       MockPet.findByIdAndUpdate.mockResolvedValue(mockPet);
        
        await PetService.incrementViewCount('507f1f77bcf86cd799439011');
        
        expect(MockPet.findByIdAndUpdate).toHaveBeenCalledWith(
          '507f1f77bcf86cd799439011',
         { $inc: { viewCount: 1 } },
         { new: true }
       );
     });
  });

  describe('incrementShareCount', () => {
    it('應該成功增加分享次數', async () => {
       MockPet.findByIdAndUpdate.mockResolvedValue(mockPet);
       
       await PetService.incrementShareCount('507f1f77bcf86cd799439011');
        
        expect(MockPet.findByIdAndUpdate).toHaveBeenCalledWith(
          '507f1f77bcf86cd799439011',
         { $inc: { shareCount: 1 } },
         { new: true }
       );
     });
  });
});

console.log('✅ petService 測試檔案已載入');